import App from "./src/app";
import IPCHandler from "./src/shared/IPCHandler";
import React from "react";
import ReactDOM from "react-dom";

export async function main() {
    // Port connection
    const channel = new IPCHandler();
    // Start ExtBootstrapApp
    const storage = webviewStorage;
    const domContainer = document.createElement("div");
    document.body.appendChild(domContainer);
    ReactDOM.render(<App port={channel} storage={storage} />, domContainer);
}

const webviewStorage = {
    local: {
        get: getStorage,
        set: function (key, value) {
            localStorage.setItem(key, value);
        },
        remove: function (key) {
            localStorage.removeItem(key);
        },
        clear: function () {
            localStorage.clear();
        },
    },
    onChanged: {
        addListener: function(callback) {
            addEventListener("storage", (event) => {
                const changes = {}
                const key = event.key;
                changes[key]= event.newValue
                callback(changes)
            });

        }
    }
}

function getStorage(key) {
    return new Promise(function (resolve, reject) {
        try {
            let response = {};
            var value = localStorage.getItem(key);
            response[key] = JSON.parse(value);
            resolve(response);
        } catch (error) {
            reject(error);
        }
    });
}

main()